import BreadCrumb from './BreadCrumb.vue'
import LeftNav from './LeftNav'
import PriceRangeFilter from './PriceRangeFilter'
import PriceSalesSort from './PriceSalesSort'
import GoodsList from './GoodsList'
import Paginate from './Paginate'
import {getCategory, getGoods} from '../../api/api'
export default {
    name: "List",
  data(){
      return {
        top_category: '',
        categoryMenu: '',
        curCategoryName: '',
        goodsList: '',
        goodsNums: 0,
        min_price: '',
        max_price: '',
        ordering: '',
        pageType: '',
        searchWord: '',
        isObject: true,
        currentPage:1


      }
  },
  components:{
      BreadCrumb,
      LeftNav,
      PriceRangeFilter,
      PriceSalesSort,
      GoodsList,
      Paginate
  },
  created(){
      this.getAllData()
  },
  watch:{
      '$route': 'getAllData'
  },
  methods:{
      getAllData(){
        console.log(this.$route.params,'进入从新获取',this.pageType);
          this.min_price='';
          this.max_price= '';
          this.ordering= '';
          this.pageType = '';
         if(this.$route.params.id){
                this.top_category = this.$route.params.pid || this.$route.params.id;
                this.getMenu(this.top_category); // 获取左侧菜单列表
            }else{
                this.getMenu(null); // 获取左侧菜单列表
                this.pageType = 'search';
                this.searchWord = this.$route.params.keyword;
            }
          this.getGoodsList()

      },
      getMenu(id){
          // getCategory({
          //   id: this.$route.params.id
          // }).then(
          //   (response)=>{
          //     this.categoryMenu = response.data.sub_cat;
          //     this.curCategoryName = response.data.name;
          //     console.log('left-succes');
          //   }
          // ).catch((error)=>{
          //   console.log(error)
          // })
           if(id != null){
              getCategory({
                id:this.$route.params.pid || this.$route.params.id
              }).then((response)=> {
                console.log(response.data,'aa111111111');
                console.log(this.$route.params.id);
                this.categoryMenu = response.data[0].sub_cat;
                console.log(this.categoryMenu);
                console.log('zuocezhaodao');

                this.curCategoryName = response.data[0].name;
                this.isObject = true

              }).catch(function (error) {
                console.log(error);
              });
            }else {
              getCategory({}).then((response)=> {
                // console.log(response.data[0])
                this.categoryMenu = response.data;
                this.isObject = false;

              }).catch(function (error) {
                console.log(error);
              });
            }
      },

      getGoodsList(){
          if (this.pageType === 'search') {
              getGoods({
              search: this.searchWord,
              min_price: this.min_price,
              max_price: this.max_price,
              ordering: this.ordering,
              p: this.currentPage
            }).then(
              (response)=>{
                console.log(response.data);
                this.goodsList = response.data;
                this.goodsNums = response.data.count
              }
            )
          }else {
              this.top_category = this.$route.params.id || this.$route.params.pid;
              getGoods({
                top_category: this.top_category,
                min_price: this.min_price,
                max_price: this.max_price,
                ordering: this.ordering,
                p: this.currentPage
              }).then(
                (response)=>{
                  console.log(response.data);
                  this.goodsList = response.data;
                  this.goodsNums = response.data.count
                }
              )
            }
      },
      priceRange(data){
          console.log(data.min,data.max);
          this.min_price = data.min;
          this.max_price = data.max;
          this.currentPage = 1;
          this.getGoodsList()
      },
      salesSortChange(order){
        console.log(this.ordering,'qiehuan');
        if (this.ordering === order && this.ordering === '-shop_price'){
          this.ordering = 'shop_price';
          this.getGoodsList()
        } else {
          this.ordering = order;
          this.getGoodsList()
        }

      },
      handleCurrentChange(currentPage){
          console.log('页数变化了');
          this.currentPage = currentPage;
          this.getGoodsList()
      },
      categoryChange(id){
        console.log(id)
      }

  },
}



