import {getShopCarts, updateShopCarts, delShopCarts} from '../../api/api'
export default {
    name: "ShopCart",
    data(){
              return {
              products: [],
              choice:false}
          },
    created(){
        this.getData()
    },
    computed: {
      checkAll: {
        get() {
          return this.products.every(p => p.selected);
        },
        set(val) {
          this.products.forEach(p => {
            p.selected = val;
            this.isSelected(p.goods.id, val)
          });
        },
      },
      hasSelected:{
        get() {
          return this.products.every(p => !p.selected);
        }
        },
        sum(){  // 如果计算属性写成函数，默认调用的就是get方法
                return this.products.reduce((prev,next)=>{
                if (!next.selected) return prev; // 未勾选中的不加上其价格
                return prev+next.goods.shop_price*next.nums
            },0);
            },
        // sum: {
        //     get(){
        //         alert(1);
        //         return this.products.reduce((prev,next)=>{
        //         if (!next.isSelected) return prev; // 未勾选中的不加上其价格
        //         return prev+next.productPrice*next.productCount
        //     },0);
        //     }
        // },
    },
    filters: {
        toFixed(input, param1){
            return '￥'+ input.toFixed(param1)
        }
    },
    methods: {

        remove(p){
            this.products = this.products.filter(item=>item!==p);
            delShopCarts(p.goods.id).then((response)=>{
                  // this.products = response.data;
                  // this.checkOne();
            },err=>{
                console.log(err)
            });
        },
        getData(){
          getShopCarts().then((response)=>{
                  this.products = response.data;
                  // this.checkOne();
            },err=>{
                console.log(err)
            });
        },
        updateCart(id,num){
          updateShopCarts(id,{nums:num}).then((response)=>{
                  // this.products = response.data;
                  // this.checkOne();
            },err=>{
                console.log(err)
            });
        },
      isSelected(id,s){
          updateShopCarts(id,{selected:s}).then((response)=>{
                  // this.products = response.data;
                  // this.checkOne();
            },err=>{
                console.log(err)
            });
        },
      createOrder(){
          this.$router.push({name: 'createorder'})
      }
    },

}



