export default {
    name: "BreadCrumb",
  data(){
    return {
      levelList: null
    }
  },
  created(){
      this.getBreadcrumb()
  },
  methods:{
      getBreadcrumb(){
        let matched = this.$route.matched;
        console.log(matched, 'daohang');
        this.levelList = [matched[matched.length-1]];
        // let appid = this.$route.params.id;
        // this.levelList = this.levelList.slice(1)
      }
  },
  watch:{
      $route(){
        this.getBreadcrumb()
      }
  }
}



