import cookie from '../../static/js/cookie';
import {login}  from '../../api/api'
  export default {
      name: "Login",
    data(){
    return {
      userName:'',
      parseWord:'',
      autoLogin:false,
      error:false,
      userNameError:'',
      parseWordError:''
    }
  },
  created(){
    //清除缓存
    cookie.delCookie('token');
    cookie.delCookie('name');
    //重新触发store
    //更新store数据
    this.$store.dispatch('setInfo');
  },
  methods:{
    toLogin(){
      // if(this.userName==''||this.parseWord==''){
      //   this.error = true;
      //   return
      // }
          var that = this;
          login({
              username:this.userName,
              password:this.parseWord
          }).then((response)=> {
                console.log('1111111111');
                console.log(response);
                //本地存储用户信息
                cookie.setCookie('name',this.userName,3);
                cookie.setCookie('token',response.data.token,3);
                //存储在store
                // 更新store数据
                that.$store.dispatch('setInfo');
                //跳转到首页页面
                this.$router.push({ name: 'index'})
              })
              .catch(function (error) {
                if("non_field_errors" in error){
                  that.error = error.non_field_errors[0];
                }
                if("username" in error){
                  that.userNameError = error.username[0];
                }
                if("password" in error){
                  that.parseWordError = error.password[0];
                }
              });

      //      this.$http.post('/login', {
      //   params: {
      //     userName:this.userName,
      //     parseWord:this.parseWord,
      //   }
      // })
      //   .then((response)=> {
      //     console.log(response);
      //     //本地存储用户信息
      //     cookie.setCookie('name',response.data.info.name,7);
      //     cookie.setCookie('id',response.data.info.id,7)
      //     //存储在store
      //     // 更新store数据
      //     this.$store.dispatch('setInfo');

      //     //跳转到首页页面
      //     this.$router.push({ name: 'index'})

      //   })
      //   .catch(function (error) {
      //     console.log(error);
      //   });
    },
    errorUnshow(){
      this.error = false;
    }
  },

}




