import {addMessage, getLeavingMessage, delLeavingMessage} from '../../api/api'

export default {
    name: "Message",
  data() {
  return {
    ruleForm: {
      name: '',
      resource: '',
      desc: '',
      file: ''
    },
    rules: {
      name: [
        { required: true, message: '请输入主题', trigger: 'blur' },
        { min: 1, max: 30, message: '长度在 1 到 30 个字符', trigger: 'blur' }
      ],

      resource: [
        { required: true, message: '请选择留言类型', trigger: 'change' }
      ],
      desc: [
        { required: true, message: '请填写留言内容', trigger: 'blur' }
      ]
    },
    AllMessage: ''
  };
},
created(){
  this.getAllMessage();
},
methods: {
  preview (e) {
        this.ruleForm.file = e.target.files[0]; //获取文件资源
        console.log(this.ruleForm.file);

    },
  submitForm(formName) {
    this.$refs[formName].validate((valid) => {
      if (valid) {
        alert('submit!');
        const formData = new FormData();
            formData.append('file',this.ruleForm.file);
            formData.append('subject',this.ruleForm.name);
            formData.append('message',this.ruleForm.desc);
            formData.append('message_type',this.ruleForm.resource);
            addMessage(formData).then((response)=> {
                this.getAllMessage();
            }).catch(function (error) {
                console.log(error);
            });
      } else {
        console.log('error submit!!');
        return false;
      }
    });
  },
  getAllMessage(){
    getLeavingMessage().then(
      (response)=> {
          this.AllMessage = response.data;
      }).catch(function (error) {
          console.log(error);
      });
  },
  resetForm(formName) {
    this.$refs[formName].resetFields();
  },
  delMessage(index,id){
        alert('您确定要删除选定的留言吗？');
        delLeavingMessage(id).then(
          (response)=>{
            this.AllMessage.splice(index,1);
           alert('删除成功')

          }
        ).catch(function (error) {
            console.log(error);
        });
      },
}
}



