import Vue from 'vue'
import Router from 'vue-router'
import Home from '../base/Home'
import List from '../base/list/List'
import BaseHead from '../base/header/BaseHead'
import ProductDetail from '../base/productDetail/ProductDetail'
import Footer from '../base/footer/Footer'
import app from '../base/app/app'
import Register from '../base/register/Register'
import Login from '../base/login/Login'
import UserInfo from '../base/member/UserInfo'
import Member from '../base/member/Member'
import Collection from '../base/member/Collection'
import Message from '../base/member/Message'
import Address from '../base/member/Address'
import Order from '../base/member/Order'
import OrderDetail from '../base/member/OrderDetail'
import ShopCart from '../base/shopcart/ShopCart'
import CreateOrder from '../base/shopcart/CreateOrder'
import Shophead from '../base/shopcart/Shophead'
import Index from '../base/index/Index'
import cookie from "../static/js/cookie";
import store from '../store/store'

Vue.use(Router);

const router =new Router({
  routes: [{
    path: '/',
    name:'app',
    component: app,
    children: [
      {
        path: 'login',
        name: 'login',
        components: {
          // head: loginHead,
          content: Login,
          footer: Footer
        },
        meta: {
          title: '登录',
          need_log: false
        }
      },
      {
        path: 'register',
        name: 'register',
        components: {
          // head: loginHead,
          content: Register,
          footer: Footer
        },
        meta: {
          title: '注册',
          need_log: false
        }
      },
      {
        path: 'home',
        name: 'home',
        components: {
          head: BaseHead,
          content: Home,
          footer: Footer,
          // need_log: false
        },
        children: [
          {
            path: 'list/:id',
            name: 'list',
            component: List,
            meta: {
              title: '列表',
              need_log: false
            }
          },
          {
            path: 'list/:pid/:id',
            name: 'navlist',
            component: List,
            meta: {
              title: '列表',
              need_log: false
            }
          },
          {
            path: 'search/:keyword',
            name: 'search',
            component: List,
            meta: {
              title: '搜索',
              need_log: false
            }
          },
          {
            path: 'index',
            name: 'index',
            component: Index,
            meta: {
              title: '首页',
              need_log: false
            }
          },
          {
            path: 'productDetail/:productId',
            name: 'productDetail',
            component: ProductDetail,
            meta: {
              title: '商品详情',
              need_log: false
            }
          },
          {
            path: 'member',
            name: 'member',
            component: Member,
            children: [
              {
                path: 'message',
                name: 'message',
                component: Message,
                meta: {
                  title: '我的留言',
                  need_log: true
                }
              },
              {
                path: 'address',
                name: 'address',
                component: Address,
                meta: {
                  title: '收件人信息',
                  need_log: true
                }
              },
              {
                path: 'order',
                name: 'order',
                component: Order,
                meta: {
                  title: '我的订单',
                  need_log: true
                }
              },
              {
                path: 'orderDetail/:orderId',
                name: 'orderDetail',
                component: OrderDetail,
                meta: {
                  title: '我的订单',
                  need_log: true
                }
              },
              {
                path: 'collection',
                name: 'collection',
                component: Collection,
                meta: {
                  title: '我的收藏',
                  need_log: true
                }
              },
              {
                path: 'userinfo',
                name: 'userinfo',
                component: UserInfo,
                meta: {
                  title: '用户信息',
                  need_log: true
                }
              },
            ],
          }
        ]
      },
      {
        path: 'shoppingcart',
        components: {
          head: Shophead,
          content: Home,
          footer: Footer
        },
        children: [
          {
            path: 'cart',
            name: 'cart',
            component: ShopCart,
            meta: {
              title: '购物车',
              need_log: true
            }
          },
          {
            path: 'createorder',
            name: 'createorder',
            component: CreateOrder,
            meta: {
              title: '生成订单',
              need_log: true
            }
          },
        ]
      }

    ]
  }],
  scrollBehavior (to, from, savedPosition) {
   if (savedPosition) {
      return savedPosition
   } else {
      return { x: 0, y: 0 }
   }
  }

});
//进行路由判断
router.beforeEach((to, from, next) => {
  var nextPath = cookie.getCookie('nextPath')
  console.log(nextPath)
  if(nextPath=="pay"){
    next({
      path: '/home/member/order',
    });
  }else{
    if(to!=undefined){
      if(to.meta.need_log){
        console.log(to.meta.need_log)
        if(!store.state.userInfo.token){
          next({
            path: '/login',
          });
        }else {
          next();
        }
      }else {
        if (to.path === '/') {
          next({
            path: '/home/index',
          });
        }else {
          next();
        }
      }
    }else {
      if (to.path === '/') {
        next({
          path: '/home/index',
        });
      }else {
        next();
      }
    }
  }});
//修改网页标题
router.afterEach((to, from, next) => {
  document.title = to.matched[to.matched.length - 1].meta.title;
});

export default router;




