import {getIndexGoods} from '../../api/api'
export default {
    name: "ListArea",
    data(){
      return {
        allIndexGoods:'',
        goodsAd:''
      }
    },
    created(){
      this.getAll()
    },
    methods:{
      getAll(){
        getIndexGoods().then((response)=>{
            this.allIndexGoods = response.data;
            // this.goodsAd = this.allIndexGoods.ad[0]
        }).catch(function (error) {
            console.log(error);
        });
      }
    }
}



