import {getGoods} from '../../api/api'

export default {
    name: "HotGoods",

  data(){
    return {
      allHotGoods:''
    }
  },
  created(){
      this.getHotGoods()
  },
  methods:{
      getHotGoods(){
        getGoods({
          'is_hot': true
        }).then(
          (response)=>{
            this.allHotGoods = response.data.results
          }
        ).catch(function (error) {
            console.log(error);
        });
      },
    clickGoods(id){
        // console.log('aaaa');
        // this.$emit('on-change',id);
        this.$router.push({name:'productDetail',params:{productId:id}})
        // this.minPrice = '';
        // this.maxPrice = '';
      },
  }
}



