import {getGoods} from '../../api/api'
export default {
    name: "NewProducts",
    data(){
      return {
        goodsList:''
      }
    },
  created(){
    this.getGoodsList()
  },
    methods:{
      getGoodsList(){
              getGoods({
                is_new:true
              }).then((response)=>{
                  this.goodsList = response.data;
                }
              )

      },
    }
}



