import {getIndexBanner} from '../../api/api'
export default {
      name: "MyBanner",
    data(){
      return  {allBanners:''}
   },
    created(){
        this.getBanner()
    },
    methods:{
        getBanner(){
        getIndexBanner().then((response)=> {
        // console.log(response);
        this.allBanners = response.data;
        // console.log(this.allBanners);
        // console.log('succes');
      })
        .catch(function (error) {
          console.log(error);
        })
        }
    },
}



