import {
  provinceAndCityData,
  regionData,
  provinceAndCityDataPlus,
  regionDataPlus,
  CodeToText,
  TextToCode
} from 'element-china-area-data'
import {addAddress, getAddress, changeAddress, delAddress} from '../../api/api'
export default {
  name: "Address",
  components: {
    regionData
  },
  data() {
    return {
      options: regionData,
      selectedOptions: ["130000", "130400", "130403"],
      mobile:'',
      name: '',
      detailAddress: '',
      allAddress: ''

    }
  },
  created(){
      this.getAllAddress()
  },
  methods: {
    handleChange(value) {
      console.log(value);
      console.log(CodeToText[value[0]], CodeToText[value[1]], CodeToText[value[2]]);
    },
    submitForm(){
      addAddress({
        province: CodeToText[this.selectedOptions[0]],
        city: CodeToText[this.selectedOptions[1]],
        district: CodeToText[this.selectedOptions[2]],
        address: this.detailAddress,
        signer_name: this.name,
        signer_mobile: this.mobile,
      }).then(
            (response)=>{
              alert('创建成功');
            }
          ).catch(function (error) {
              console.log(error);
          });
    },
    getAllAddress(){
      getAddress().then(
            (response)=>{
              this.allAddress = response.data;


              for (let i=0;i<this.allAddress.length;i++)
                  {
                      this.allAddress[i].new_address = [TextToCode[this.allAddress[i].province].code,
                                                          TextToCode[this.allAddress[i].province][this.allAddress[i].city].code,
                                                          TextToCode[this.allAddress[i].province][this.allAddress[i].city][this.allAddress[i].district].code];

                  }
            }
          ).catch(function (error) {
              console.log(error);
          });
    },
    submitFormChange(address){
      alert('您确定要修改选定的收货地址信息吗？');
      console.log(address);
      let param = {
        province: CodeToText[address.new_address[0]],
        city: CodeToText[address.new_address[1]],
        district: CodeToText[address.new_address[2]],
        address: address.address,
        signer_name: address.signer_name,
        signer_mobile: address.signer_mobile,
      };
      console.log(param);
      changeAddress(address.id,{
        province: CodeToText[address.new_address[0]],
        city: CodeToText[address.new_address[1]],
        district: CodeToText[address.new_address[2]],
        address: address.address,
        signer_name: address.signer_name,
        signer_mobile: address.signer_mobile,
        id:address.id
      }).then(
            (response)=>{
              alert('修改成功');
            }
          ).catch(function (error) {
              console.log(error);
          });
    },
    submitFormDel(id, index){
          alert('您确定要删除选定的收货地址吗？');
          delAddress(id).then(
            (response)=>{
              this.allAddress.splice(index,1);
             alert('删除成功')

            }
          ).catch(function (error) {
              console.log(error);
          });
        },
  }
}



