import {getOrders, delOrders} from '../../api/api'
export default {
    name: "Order",
  data(){
    return {
      AllOrders: ''
    }
  },
  created(){
      this.getAllOrders()
  },
  methods:{
      getAllOrders(){
        getOrders().then((response)=> {
                this.AllOrders = response.data;
            }).catch(function (error) {
                console.log(error);
            });
      },
    delOrder(index,id){
        alert('您确定要删除选定的订单吗？');
        delOrders(id).then(
          (response)=>{
            this.AllOrders.splice(index,1);
           alert('删除成功')

          }
        ).catch(function (error) {
            console.log(error);
        });
      },
}

}



