import {getGoodsDetails, getFav, addFav, delFav, addShopCarts} from '../../api/api'
import HotGoods from './HotGoods'
import BreadCrumb from '../list/BreadCrumb'
import cookie from "../../static/js/cookie";
  export default {
      name: "ProductDetail",
    components:{HotGoods,BreadCrumb},
    data(){
        return {
          goodsCount:1,
          min: 1,
          goodId: '',
          allDetails:'',
          isFav: false,

        }
    },
    created(){
        this.goodId = this.$route.params.productId;
        var gid = this.goodId;
        if (cookie.getCookie('token')){
          console.log('得到token',cookie.getCookie('token'));
          getFav(this.goodId).then((response)=>{
            this.isFav = true
          }).catch(function (error) {
            console.log(error)
          })
        }
        this.getDetails()
    },
    watch:{
        '$route': 'getNewFav'
    },
    methods:{
        getDetails(){
          getGoodsDetails(this.goodId).then((response)=>
          {
            console.log(response.data);
            this.allDetails = response.data
          }).catch(function (error) {
              console.log(error);
          });
        },
        hotGoodsClick(id){
            this.goodId = id;
          this.isFav = false;
          if (cookie.getCookie('token')){
            console.log('得到token',cookie.getCookie('token'));
            getFav(this.goodId).then((response)=>{
              this.isFav = true
            }).catch(function (error) {
              console.log(error)
            })
          }
          this.getDetails()

        },
        addCollect(){
          addFav({
            goods: this.goodId
          }).then((response)=>{
            this.isFav = true;
            alert('已成功收藏')
          }).catch(function (error) {
              console.log(error);
          });
        },
        delCollect(){
          delFav( this.goodId
          ).then((response)=>{
            this.isFav = false;

          }).catch(function (error) {
              console.log(error);
          });
        },
        getNewFav(id){
          this.goodId = this.goodId = this.$route.params.productId;
          this.isFav = false;
          if (cookie.getCookie('token')){
            console.log('得到token',cookie.getCookie('token'));
            getFav(this.goodId).then((response)=>{
              this.isFav = true
            }).catch(function (error) {
              console.log(error)
            })
          }
          this.getDetails()
          },
        addShopCart(){
          addShopCarts({
            nums: this.goodsCount,
            goods: this.goodId,
            selected: false
          }).then((response)=>{

            alert('已加入购物车')
          }).catch(function (error) {
              console.log(error);
          });
        }
    }
  }



