export default {
    name: "LeftNav",
  props:{
      categoryMenu: {
        default: function() {
            return {};
          }
      },
      goodsNums: { //商品数量
          type: Number,
          default: 0
      },
      isArray:{
          default: function () {
            return false
          }
      },
      isObject: false,
      curCategoryName:''
  },
  created(){
      console.log('到达左侧导航 ')
  },
  methods:{
      getGoods(id){
        // this.$emit('on-change',id)
        this.$router.push({name:'list',params:{id:id}})
      }
  }
}



