import {getUserDetail, changeUserDetail} from '../../api/api'
export default {
  name: "UserInfo",
  data() {
    return {
      userDetail: {
        name: '',
        birthday: '',
        email: '',
        mobile: '',
        gender: 'female',
      },
      rules: {
        name: [
          {required: true, message: '请输入姓名', trigger: 'blur'},
          {min: 2, max: 30, message: '长度在 2 到 30 个字符', trigger: 'blur'}
        ],
        email: [
          {required: true, message: '请输入邮箱', trigger: 'blur'},
          // { min: 3, max: 5, message: '长度在 3 到 5 个字符', trigger: 'blur' }
        ],
        mobile: [
          {required: true, message: '请输入手机号', trigger: 'blur'},
          {min: 11, max: 11, message: '长度为11位', trigger: 'blur'}
        ],
        birthday: [
          {type: 'date', required: false, message: '请选择日期', trigger: 'change'}
        ],
        gender: [
          {required: true, message: '请选择性别', trigger: 'change'}
        ]
      },

    }
  },
  created(){
    this.getDetails()
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          // alert('submit!');
        } else {
          console.log('error submit!!');
          return false;
        }
      });
      console.log(this.userDetail);
      changeUserDetail(this.userDetail).then((response)=>{
        alert('修改成功')
      }).catch(function (error) {
          console.log(error)
      })
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    getDetails(){
      getUserDetail().then((response)=>{
        this.userDetail = response.data
      }).catch(function (error) {
          console.log(error)
      })
    },
    isMale(){
      if (this.userDetail.gender === 'male') {
        return true
      }
    },
    isFemale(){
      if (this.userDetail.gender === 'female') {
        return true
      }
    },
  }
}



