import {getAllFavs, delFav} from '../../api/api'
  export default {
      name: "Collection",
    data() {
    return {
     allCollections:''
    }
  },
    created(){
      this.getCollections()
    },
    methods:{
        getCollections(){
          getAllFavs().then(
            (response)=>{
              console.log(response.data);
              this.allCollections = response.data
            }
          ).catch(function (error) {
              console.log(error);
          });
        },
      delCollection(index,id){
          alert('您确定要从收藏夹中删除选定的商品吗？');
          delFav(id).then(
            (response)=>{
              this.allCollections.splice(index,1);
             alert('删除成功')

            }
          ).catch(function (error) {
              console.log(error);
          });
        },
    },
  }



