import {register,getMessage}  from '../../api/api'
export default {
  name: "Register",
  data() {
    let checkAge = (rule, value, callback) => {
      if (!value) {
        return callback(new Error('年龄不能为空'));
      }
      setTimeout(() => {
        if (!Number.isInteger(value)) {
          callback(new Error('请输入数字值'));
        } else {
          if (value < 18) {
            callback(new Error('必须年满18岁'));
          } else {
            callback();
          }
        }
      }, 1000);
    };

    let validateMobilePhone = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('手机号不可为空'));
      } else {
        if (this.ruleForm2.mobile !== '') {
          let reg=/^1[3456789]\d{9}$/;
          if(!reg.test(value)){
            callback(new Error('请输入有效的手机号码'));
          }
        }
        callback();
      }
    };
    let validateCode = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('验证码不可为空'));
      } else {
        if (this.ruleForm2.code !== '') {
          let reg=/^\d{4}$/;
          if(!reg.test(value)){
            callback(new Error('请输入有效的验证码'));
          }
        }
        callback();
      }
    };


    let validatePass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入密码'));
      } else if(value.length < 8){
            callback(new Error('密码长度不够'));
          }else if (this.ruleForm2.checkPass !== '') {
            this.$refs.ruleForm2.validateField('checkPass');
        }
        callback();

    };
    let validatePass2 = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请再次输入密码'));
      } else if (value.length < 8){
            callback(new Error('密码长度不够'));
      }else if (value !== this.ruleForm2.pass) {
        callback(new Error('两次输入密码不一致!'));
      }  else {
        callback();
      }
    };
    return {
      ruleForm2: {
        pass: '',
        checkPass: '',
        age: '',
        mobile: '',
        code: '',
        checked: false,

      },
      rules2: {
        pass: [
          { validator: validatePass, trigger: 'blur' }
        ],
        checkPass: [
          { validator: validatePass2, trigger: 'blur' }
        ],
        age: [
          { validator: checkAge, trigger: 'blur' }
        ],
        mobile: [
          { validator: validateMobilePhone, trigger: 'blur' }
        ],
        code: [
          { validator: validateCode, trigger: 'blur' }
        ],

      },
      getMessageText: '获取验证码',
      hasSend: false,
      error:{
              mobile:'',
              password:'',
              username:'' ,
              code:'',
          }
    };
  },
  watch:{
      getMessageText(){

        this.hasSend = true

      }

  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid && this.ruleForm2.checked) {
          alert('submit!');
          this.isRegister()
        } else {
          console.log('error submit!!');
          return false;
        }
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
     isRegister(){
          var that = this;
          register({
              password:that.ruleForm2.pass,
              username:that.ruleForm2.mobile ,
              code:that.ruleForm2.code,
          }).then((response)=> {
            // cookie.setCookie('name',response.data.username,7);
            // cookie.setCookie('token',response.data.token,7)
            // //存储在store
            // // 更新store数据
            // that.$store.dispatch('setInfo');
            //跳转到首页页面
            this.$router.push({ name: 'home'})

        })
        .catch(function (error) {
          that.error.mobile = error.username?error.username[0]:'';
          that.error.password = error.password?error.password[0]:'';
          that.error.username = error.mobile?error.mobile[0]:'';
          that.error.code = error.code?error.code[0]:'';
        });
      },
      seedMessage(){
          var that = this;
          //开启倒计时
          var countdown=60;


           settime();

          function settime() {
              if (countdown === 0) {
                  that.getMessageText="获取验证码";
                  countdown = 60;

                  return;
              } else {
                  that.getMessageText="重新发送" + countdown + "";
                  countdown--;
                  
              }
                  // if (that.getMessageText !== "获取验证码") {
                  //   this.hasSend=true;
                  // }
              setTimeout(function() {
                  settime()
               },1000);

              }
          getMessage({
                  mobile:that.ruleForm2.mobile
              }).then((response)=> {

            })
            .catch(function (error) {

        });

      }
  }
}




