import {getOrderDetail} from '../../api/api'

export default {
  name: "OrederDetail",
  data() {
    return {
      orderId: '',
      order: '',
    }
  },
  created() {
    this.getOrder()

  },
  methods: {
    getOrder() {
      this.orderId = this.$route.params.orderId;
      getOrderDetail(this.orderId).then(
        (response) => {
          this.order = response.data
        }
      ).catch(function (error) {
        console.log(error)
      })
    },
    submitOrder(){
      window.location.href=this.order.alipay_url;
    }
  }
}



