import {getShopCarts, addOrders,getAddress} from '../../api/api'

export default {
    name: "ShopCart",
    data(){
              return {
              products: [],
              allAddress: '',
              addressIsActive: '',
              payIsActive: 'weixin',
              address: '',
              signer_name: '',
              signer_mobile: '',
              pay_type: 'wechat',
              sumPrice: 0,
              post_script: '无',
              hasSelected: true,
              }
          },
    created(){
      this.getData();
      this.getAllAddress()
          },
    computed: {
        sum(){  // 如果计算属性写成函数，默认调用的就是get方法
                return this.products.reduce((prev,next)=>{
                if (!next.selected) return prev; // 未勾选中的不加上其价格
                this.sumPrice = prev+next.goods.shop_price*next.nums
                return this.sumPrice
            },0);
            },
        // sum: {
        //     get(){
        //         alert(1);
        //         return this.products.reduce((prev,next)=>{
        //         if (!next.isSelected) return prev; // 未勾选中的不加上其价格
        //         return prev+next.productPrice*next.productCount
        //     },0);
        //     }
        // },
    },
    filters: {
        toFixed(input, param1){
            return '￥'+ input.toFixed(param1)
        }
    },
    methods: {

      // remove(p) {
      //   this.products = this.products.filter(item => item !== p);
      //   delShopCarts(p.goods.id).then((response) => {
      //     // this.products = response.data;
      //     // this.checkOne();
      //   }, err => {
      //     console.log(err)
      //   });
      // },
      getData() {
        getShopCarts().then((response) => {
          this.products = response.data;
          this.products = this.products.filter(item => item.selected);
          // this.checkOne();
        }, err => {
          console.log(err)
        });
      },
      // updateCart(id, num) {
      //   updateShopCarts(id, {nums: num}).then((response) => {
      //     // this.products = response.data;
      //     // this.checkOne();
      //   }, err => {
      //     console.log(err)
      //   });
      // },
      // isSelected(id, s) {
      //   updateShopCarts(id, {selected: s}).then((response) => {
      //     // this.products = response.data;
      //     // this.checkOne();
      //   }, err => {
      //     console.log(err)
      //   });
      // },
      getAllAddress() {
        getAddress().then(
          (response) => {
            this.allAddress = response.data;
          }
        ).catch(function (error) {
          console.log(error);
        });
      },
      getThisAddress(id, address){
        // console.log(address);
        this.hasSelected = false;
        this.addressIsActive = id;
        this.address = address.province+address.city+address.district+address.address;
        this.signer_name = address.signer_name;
        this.signer_mobile = address.signer_mobile
      },
      getPayChoiceWX(){
        this.payIsActive = 'weixin';
        this.pay_type = 'alipay'
      },
      getPayChoiceZFB(){
        this.payIsActive = 'zhifubao';
        this.pay_type = 'wechat'
      },
      submitOrder(){
        // console.log(this.pay_type,this.signer_mobile);
        addOrders({
          pay_type: this.pay_type,
          post_script: this.post_script,
          order_mount: this.sumPrice,
          address: this.address,
          signer_name: this.signer_name,
          signer_mobile: this.signer_mobile,
        }).then(
          (response) => {
            // this.allAddress = response.data;
            alert('订单创建成功');
            window.location.href=response.data.alipay_url;
          }
        ).catch(function (error) {
          console.log(error);
        });
      }
    }
}



