import axios from 'axios';



let localhost = 'http://39.107.94.185:8000';

// 获取商品类目
export const getCategory = params => {
  // console.log(params,'jiequ');
  if ('id' in params){

    return axios.get(`${localhost}/categorys/?id=`+ params.id)
  }
  else {
    console.log('准备请求分类数据');
    return axios.get(`${localhost}/categorys/`);


    // return axios.get(`./abc.json`)
  }
};

// 获取商品
export const getGoods = params => {
  return axios.get(`${localhost}/goods/`,{params:params});};
//获取商品详情
export const getGoodsDetails = goodId => {
  return axios.get(`${localhost}/goods/`+goodId+'/')
};
